











import { QuizInstance } from '@/models/QuizInstanceModel';
import Vue, { PropType } from 'vue';

export default Vue.extend({
  name: 'QuizInstanceCard',
  props: {
    quiz: {
      type: Object as PropType<QuizInstance>,
      required: true,
    },
  },
  data() {
    return {
      toBeRemove: false,
      toFadeIn: false,
      hasFadeIn: false,
    };
  },
  mounted() {
    setTimeout(() => {
      this.toFadeIn = true;
      setTimeout(() => {
        this.hasFadeIn = true;
        this.toFadeIn = false;
      }, 200);
    }, 0);
  },
  methods: {
    removeMe() {
      this.toBeRemove = true;
      setTimeout(() => {
        this.$emit('removeMe');
        this.toBeRemove = false;
      }, 200);
    },
    applyMe() {
      this.$emit('applyMe');
    },
  },
});
