import api from '@/apis/BaseApi';
import { QuizTemplateModel } from '@/apis/models/QuizModel';

export default class QuizTemplateApi {
  static async getTemplate(): Promise<QuizTemplateModel> {
    const result = await api.get<QuizTemplateModel>('quiz/template');
    return result.data;
  }

  static async getTemplates(): Promise<QuizTemplateModel[]> {
    const result = await api.get<QuizTemplateModel[]>('quiz/templates');
    return result.data;
  }
}
