import { QuizImage, ClickAreaModel, QuizTemplateModel } from '@/apis/models/QuizModel';
import Utils from '@/libs/utils';

export interface QuizInstanceModel extends QuizTemplateModel{
  uuid: string;
}

export class QuizInstance implements QuizInstanceModel {
  uuid: string;

  templateId: string;

  templateName: string;

  answer: string;

  timeLimit: number;

  baseImage: QuizImage;

  previewImage: QuizImage;

  outputImage: QuizImage;

  clickAreas: ClickAreaModel[];

  constructor(template: QuizTemplateModel, outputImageLink: string, answer: string) {
    this.uuid = ''; // no uuid now, get uuid after upload
    this.templateId = template.templateId;
    this.templateName = template.templateName;
    this.answer = answer;
    this.timeLimit = 0;
    this.baseImage = template.baseImage;
    this.previewImage = Utils.deepcopy(template.baseImage);
    this.outputImage = Utils.deepcopy(template.baseImage);
    this.previewImage.url = outputImageLink;
    this.outputImage.url = outputImageLink;
    this.clickAreas = template.clickAreas;
  }
}
