export default class Utils {
  static deepcopy(obj: unknown) {
    return JSON.parse(JSON.stringify(obj));
  }

  static randomString(length: number): string {
    if (length < 0) return '';
    return Math.random().toString(36).substring(length);
  }

  static preloadImage(url: string): Promise<void> {
    return new Promise((resolve, reject) => {
      const preloadImg = new Image();
      preloadImg.onload = () => {
        resolve();
      };
      preloadImg.onerror = () => {
        reject(new Error(`Failed to load image from ${url}`));
      };
      preloadImg.src = url;
    });
  }

  static fileToDataUrl(file: File | Blob): Promise<string> {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = (e) => {
        if (e && e.target && typeof (e.target.result) === 'string') {
          const dataUrl = e.target.result;
          resolve(dataUrl);
        } else {
          reject(new Error('Failed to read file as dataUrl'));
        }
      };
      reader.readAsDataURL(file);
    });
  }

  static getCanvasBlob(canvas: HTMLCanvasElement) {
    return new Promise<Blob>(((resolve, reject) => {
      canvas.toBlob((blob: Blob | null) => {
        if (blob === null) {
          reject(new Error('Unable to convert canvas to blob'));
        } else {
          resolve(blob);
        }
      }, 'image/png');
    }));
  }
}
