import api from '@/apis/BaseApi';
import { QuizInstance } from '@/models/QuizInstanceModel';

export default class QuizInstanceApi {
  static async uploadInstance(instance: QuizInstance): Promise<string> {
    const result = await api.post('quiz/instance', JSON.stringify(instance));
    return result.data.quizId;
  }

  static async updateInstance(instance: QuizInstance): Promise<string> {
    const result = await api.put(`quiz/instance/${instance.uuid}`, JSON.stringify(instance));
    return result.data;
  }
}
