
































import StorageApi from '@/apis/StorageApi';
import Vue, { PropType } from 'vue';
import imagedownload from '@/assets/image-download.jpg';
import { StageEventBus } from '@/libs/EventBus';
import { ContentModel } from '@/apis/models/QuizModel';
import Utils from '@/libs/utils';
import _ from 'lodash';

export default Vue.extend({
  name: 'StageImage',
  props: {
    width: {
      type: Number,
      // required: true,
      default: 200,
    },
    height: {
      type: Number,
      // required: true,
      default: 200,
    },
    fields: {
      type: Object as PropType<ContentModel>,
      // required: true,
      default: () => ({
        label: '',
        type: 'text',
        value: '',
        check: false,
        options: [],
      } as ContentModel),
    },
    imageSize: {
      type: Number,
      default: 80,
    },
  },
  data() {
    return {
      isDragging: false,
      errorMessage: '',
      displayPlaceholder: true,
      imagedownload,
      isLoading: false,
      currentUploadId: 0,
    };
  },
  computed: {
    getClasses(): object {
      return { isDragging: this.isDragging };
    },
    imageUrl(): string {
      return this.fields.value;
    },
  },
  watch: {
    imageUrl(newUrl: string) {
      if (newUrl !== '') {
        this.isLoading = true;
        Utils.preloadImage(newUrl).catch(() => {
          this.showErrorMessage('無法讀取檔案');
        }).finally(() => {
          this.isLoading = false;
        });
      }
    },
  },
  mounted() {
    StageEventBus.$on('reset-stage', this.reset);
    StageEventBus.$on('before-screenshot', this.beforeScreenshot);
    StageEventBus.$on('after-screenshot', this.afterScreenshot);
  },
  methods: {
    dragOver() {
      this.isDragging = true;
    },
    dragLeave() {
      this.isDragging = false;
    },
    async drop(e: DragEvent) {
      this.isDragging = false;
      if (!e.dataTransfer) {
        return;
      }

      const { files } = e.dataTransfer;
      this.errorMessage = '';
      // allows only 1 file
      if (files.length > 0) {
        const file = files[0];
        // allows image only
        if (file.type.indexOf('image/') >= 0) {
          this.uploadImage(file);
        } else {
          this.showErrorMessage('錯誤的檔案格式');
        }
      }
    },
    async uploadImage(file: File) {
      this.isLoading = true;

      const myUploadId = _.random(0, 1e10);
      this.currentUploadId = myUploadId;

      // upload image to storage
      try {
        const link = await StorageApi.uploadImage(file);
        if (this.currentUploadId === myUploadId) {
          this.fields.value = link;
        }
      } catch (e) {
        this.showErrorMessage('無法讀取檔案');
      } finally {
        this.isLoading = false;
      }
    },
    showErrorMessage(message: string) {
      this.errorMessage = message;
      setTimeout(() => {
        this.errorMessage = '';
      }, 2000);
    },
    reset() {
      this.isDragging = false;
      this.errorMessage = '';
      this.displayPlaceholder = true;
      this.isLoading = false;
      this.currentUploadId = 0;
    },
    beforeScreenshot() {
      this.displayPlaceholder = false;
    },
    afterScreenshot() {
      this.displayPlaceholder = true;
    },
  },
});
