






import Vue, { PropType } from 'vue';
import TextArea from '@/components/quiz_maker/TextArea.vue';
import { StageEventBus } from '@/libs/EventBus';
import { ContentModel } from '@/apis/models/QuizModel';

export default Vue.extend({
  name: 'StageText',
  components: {
    TextArea,
  },
  props: {
    width: {
      type: Number,
      required: true,
    },
    height: {
      type: Number,
      required: true,
    },
    fields: {
      type: Object as PropType<ContentModel>,
      required: false,
      default: () => ({
        label: '',
        type: 'text',
        value: '',
        check: true,
        options: [],
      } as ContentModel),
    },
    fontSize: {
      type: Number,
      required: false,
      default: 24,
    },
  },
  data() {
    return {
      displayPlaceholder: true,
    };
  },
  mounted() {
    StageEventBus.$on('reset-stage', this.reset);
    StageEventBus.$on('before-screenshot', this.beforeScreenshot);
    StageEventBus.$on('after-screenshot', this.afterScreenshot);
  },
  methods: {
    reset() {
      // reset data here
    },
    beforeScreenshot() {
      this.displayPlaceholder = false;
    },
    afterScreenshot() {
      this.displayPlaceholder = true;
    },
  },
});
