














































// @ is an alias to /src
import Vue from 'vue';
import { mapState } from 'vuex';
import html2canvas from 'html2canvas'; // 'html2canvas/dist/html2canvas';

import Stage from '@/components/quiz_maker/Stage.vue';
import OptionPanel from '@/components/quiz_maker/OptionPanel.vue';
import QuizList from '@/components/quiz_maker/QuizList.vue';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
// import { StageOption } from '@/libs/options';
import { StageEventBus } from '@/libs/EventBus';
import Utils from '@/libs/utils';

import { QuizInstance, QuizInstanceModel } from '@/models/QuizInstanceModel';

import QuizGroupApi from '@/apis/QuizGroupApi';
import StorageApi from '@/apis/StorageApi';
import QuizTemplateApi from '@/apis/QuizTemplateApi';
import QuizInstanceApi from '@/apis/QuizInstanceApi';
import { QuizTemplateModel } from '@/apis/models/QuizModel';
import { QuizGroup } from '@/apis/models/QuizGroupModel';

export default Vue.extend({
  name: 'QuizMaker',
  components: {
    Stage,
    OptionPanel,
    QuizList,
  },
  data() {
    return {
      stageWidth: 300,
      quizTemplates: [] as QuizTemplateModel[], // quiz templates are defined in backend database
      quizInstances: [] as QuizInstance[], // quiz instances are created based on quiz templates
      templateOnStage: null as QuizTemplateModel | QuizInstanceModel | null, // the template which user is editing
      currentTemplateIndex: 0,
      copyMessageDisplay: false,
      copyMessageTimeoutId: 0,
    };
  },
  computed: {
    outputWidth(): number {
      if (this.templateOnStage) {
        return this.templateOnStage.baseImage.width;
      }

      return 0;
    },
    quizGroupId(): string {
      return this.$store.state.currentQuizGroup.uuid;
    },
    quizGroupShortId(): string {
      return this.$store.state.currentQuizGroup.shortId;
    },
    ...mapState({
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      localOptions: (state: any) => state.localOptions,
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      globalOptions: (state: any) => state.globalOptions,
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      stageAnswer: (state: any) => state.stageAnswer,
    }),
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
  },
  mounted() {
    this.$store.commit('resetStage');
    this.loadQuizTemplate();
    this.loadQuizGroup(this.quizGroupId);
  },
  methods: {
    async addToGroup() {
      if (this.templateOnStage === null) return;

      this.$store.commit('updateLoading', true);
      try {
        const template = this.templateOnStage;

        const imageLink = await this.getScreenshotUrl();

        const instance = await this.createInstance(template, imageLink);

        this.appendInstance(instance);

        const currTempalte = this.quizTemplates.find((temp: QuizTemplateModel) => instance.templateId === temp.templateId);
        this.applyTemplateToStage(currTempalte);
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    async updateGroupMember() {
      if (this.templateOnStage === null) return;

      this.$store.commit('updateLoading', true);
      try {
        const instance = this.templateOnStage as QuizInstanceModel;

        const imageLink = await this.getScreenshotUrl();

        await this.updateInstanceOnStage(instance, imageLink);

        // update instance in the quizInstances list
        const instanceIndex = this.quizInstances.map((inst: QuizInstanceModel) => inst.uuid).indexOf(instance.uuid);
        // this.quizInstances[instanceIndex] = instance;
        this.$set(this.quizInstances, instanceIndex, instance);

        const currTempalte = this.quizTemplates.find((temp: QuizTemplateModel) => instance.templateId === temp.templateId);
        this.applyTemplateToStage(currTempalte);
      } finally {
        this.$store.commit('updateLoading', false);
      }
    },
    /**
     * getScreenshotUrl
     * 1. screenshot the stage
     * 2. transform the screenshot into File
     * 3. upload the File to cloud storage
     * 4. and then get the url
     */
    async getScreenshotUrl() {
      // prepare contents and screenshots
      const canvas = await this.screenshotStage();
      const blob = await Utils.getCanvasBlob(canvas);
      const imageLink = await StorageApi.uploadImage(blob, true);
      return imageLink;
    },
    /**
     * screenshotStage
     * convert Stage to png, i.e. do screenshot for Stage
     * return canvas element
     */
    async screenshotStage(): Promise<HTMLCanvasElement> {
      // add option type to get the image version
      // if not provided the promise will return
      // the canvas.
      const options = {
        useCORS: true,
        scale: this.outputWidth / this.stageWidth, // resize the canvas width to 1040 for better resolution
      };
      const el = document.querySelector('#printMe') as HTMLElement;
      if (!el) {
        throw new Error('unable to find printMe element');
      }

      StageEventBus.$emit('before-screenshot');
      await this.$nextTick(); // wait for 'before-screenshot'
      const canvas = await html2canvas(el, options)
        .finally(() => StageEventBus.$emit('after-screenshot'));
      return canvas;
    },
    /**
     * loadQuizTemplate
     * load quiz templates from backend
     */
    loadQuizTemplate() {
      QuizTemplateApi.getTemplates().then((data: QuizTemplateModel[]) => {
        this.quizTemplates = data;

        this.applyTemplateToStage(this.quizTemplates[0]);
      });
    },
    /**
     * loadQuizGroup
     * load quiz list of a quiz group from backend
     */
    loadQuizGroup(quizGroupId: string) {
      if (quizGroupId) {
        QuizGroupApi.getQuizGroup(this.$store.state.currentWorkspace.uuid, quizGroupId).then((quizGroup: QuizGroup) => {
          this.quizInstances = quizGroup.quizList;
        });
      } else {
        // redirect to safe place
        this.$router.push(this.$store.state.homepageRoute);
      }
    },
    /**
     * createInstance
     * generate a instance based on quiz tempalte, and upload it to backend database
     */
    async createInstance(template: QuizTemplateModel, imageLink: string): Promise<QuizInstance> {
      if (!template) {
        throw new Error('[QuizMaker/createInstance] Invalid template');
      }

      const instance = new QuizInstance(template, imageLink, this.stageAnswer);
      instance.uuid = await QuizInstanceApi.uploadInstance(instance);

      return instance;
    },
    /**
     * updateInstance
     * update contents of a existed instance based on changes on Stage
     */
    async updateInstanceOnStage(instance: QuizInstanceModel, imageLink: string): Promise<void> {
      if (!instance || !('uuid' in instance)) {
        throw new Error('[QuizMaker/updateInstance] Invalid instance');
      }

      const updateInst = instance;
      updateInst.answer = this.stageAnswer;
      updateInst.previewImage.url = imageLink;
      updateInst.outputImage.url = imageLink;
      await QuizInstanceApi.updateInstance(updateInst);
    },
    /**
     * appendInstance
     * add a new quiz instance to quizInstance array
     */
    appendInstance(instance: QuizInstance) {
      this.quizInstances.push(instance);
      this.syncQuizList();
    },
    /**
     * syncQuizList
     * upload current quizInstances
     * call this function whenever quizInstances is modified (add, remove, reorder)
     */
    syncQuizList() {
      QuizGroupApi.setQuizList(this.$store.state.currentWorkspace.uuid, this.quizGroupId, this.quizInstances);
    },
    applyTemplateToStage(template: QuizTemplateModel| undefined) {
      // reset stage
      StageEventBus.$emit('reset-stage');
      this.$store.commit('resetStage');

      if (template) {
        this.templateOnStage = Utils.deepcopy(template);
      } else {
        this.templateOnStage = Utils.deepcopy(this.quizTemplates[0]);
      }
    },
    applyInstanceToStage(quizInstance: QuizInstanceModel | undefined) {
      // reset stage
      StageEventBus.$emit('reset-stage');
      this.$store.commit('resetStage');

      if (quizInstance) {
        this.templateOnStage = Utils.deepcopy(quizInstance) as QuizInstanceModel;
        this.$store.commit('updateStageAnswer', quizInstance.answer);
      }
    },
  },
});
