





























import Vue from 'vue';
import StageText from '@/components/quiz_maker/StageText.vue';
import StageImage from '@/components/quiz_maker/StageImage.vue';
import { StageEventBus } from '@/libs/EventBus';
import { ContentModel } from '@/apis/models/QuizModel';
import { StageOptionModel } from '@/libs/options';

export default Vue.extend({
  name: 'StageBlock',
  components: {
    StageText,
    StageImage,
  },
  props: {
    width: {
      type: Number,
      required: true,
    },
    height: {
      type: Number,
      required: true,
    },
    label: {
      type: String,
      required: false,
      default: '',
    },
    fields: {
      type: Array as () => Array<ContentModel>,
      required: false,
      default: () => [] as Array<ContentModel>,
    },
  },
  data() {
    return {
      displayAnswer: true,
    };
  },
  computed: {
    wrapperStyle(): object {
      return {
        width: `${this.width}px`,
        height: `${this.height}px`,
      };
    },
    textField(): ContentModel {
      return this.fields.filter((field: ContentModel) => field.type === 'text')[0];
    },
    textFieldFontSize(): StageOptionModel {
      return this.textField.options.filter((option: StageOptionModel) => option.label === '大小')[0];
    },
    imageField(): ContentModel {
      return this.fields.filter((field: ContentModel) => field.type === 'image')[0];
    },
    imageFieldSize(): StageOptionModel {
      return this.imageField.options.filter((option: StageOptionModel) => option.label === '尺寸')[0];
    },
    quizAnswer(): string {
      return this.$store.state.stageAnswer;
    },

    isAnswer(): boolean {
      return String(this.quizAnswer) === this.label;
    },
  },
  mounted() {
    StageEventBus.$on('reset-stage', this.reset);
    StageEventBus.$on('before-screenshot', this.beforeScreenshot);
    StageEventBus.$on('after-screenshot', this.afterScreenshot);
  },
  methods: {
    reset() {
      this.displayAnswer = true;
    },
    beforeScreenshot() {
      this.displayAnswer = false;
    },
    afterScreenshot() {
      this.displayAnswer = true;
    },
    chooseAnswer() {
      this.$store.commit('updateStageAnswer', this.label);
    },
    editOption() {
      this.$store.commit('setLocalOption', [this.textField, this.imageField]);
    },
  },
});
